/*处理事件共通弹窗*/
<style>
  .break-line{
    width: 100%;
    height: 1px;
    margin-top:5px;
    margin-bottom: 5px;
    background-color: #d7dde4;
  }
  .process-modal .ivu-modal-body {
    padding: 10px;
  }
  .thin .ivu-steps-main .ivu-steps-title{
    font-weight: 500 !important;
  }
</style>
<template>
  <div>
    <Modal :styles="{top: '20px'}" v-model="isShow" class="process-modal" width="850" title="处理事件">
      <Row style="height: 450px">
        <Col style="width: 45%; float: left;">
          <!-- 告警设备 -->
        <div ref="modalLeftTopCol">
          <Card dis-hover style="min-height: 220px;" class="small" >
            <p slot="title">告警设备</p>
            <div></div>
            <Row :gutter="8">
              <Col span="6" title="设备名称" style="text-align: right;">设备名称:</Col>
              <Col span="18">{{sensor.name}}</Col>
            </Row>
            <Row :gutter="8">
              <Col span="6" title="安装位置" style="text-align: right;">安装位置:</Col>
              <Col span="18">{{sensor.location}}</Col>
            </Row>
            <Row :gutter="8">
              <Col span="6" title="地理位置" style="text-align: right;">地理位置:</Col>
              <Col span="18">{{sensor.address}}</Col>
            </Row>
            <Row :gutter="8">
              <Col span="6" title="设备状态" style="text-align: right;">设备状态:</Col>
              <Col span="18">{{selectedSensorStatus}}</Col>
            </Row>
            <Row :gutter="8" v-for="user in sensor.sensorUsers" :key="user.id" v-if="user.relationType == 'OWNER'">
              <Col span="6" title="联系人" style="text-align: right;">联系人:</Col>
              <Col span="4">{{user.name}} <a @click="showUserModal" title="查看更多联系人"><Icon type="ios-more"></Icon></a></Col>
              <Col span="4" title="电话" style="text-align: right;"><Icon type="ios-telephone" style="text-align: center; vertical-align: middle; margin-right: 5px;"></Icon>电话:</Col>
              <Col span="8">{{common.revertTel(user.tel)}}</Col>
            </Row>
          </Card>
        </div>
        </Col>
        <Col style="width: 55%; float: right; padding-left: 10px;">
          <!-- 事件处理 -->
          <div>
            <Card dis-hover style="height: 450px;" class="small" >
              <p slot="title">事件处理</p>
                <Steps slot="extra" :current="currentStep"  size="small" class="thin" style="width:290px; margin-right:-40px !important;margin-top:2px !important;">
                  <Step title="开始" ></Step>
                  <Step title="处理中" ></Step>
                  <Step title="完成"></Step>
                </Steps>
              <div>

                <div  style="text-align: center" v-if="sensorEvent.processStatus == 'OPEN'">
                  <Button type="warning" size="small" long @click.prevent="processEvent('START_PROCESS')">开始处理</Button>
                </div>
                <div  style="text-align: center" v-if="sensorEvent.processStatus == 'PENDING'">
                  <Radio-group v-model="sensorEvent.processConfirmation">
                    <Radio label="REAL">真实</Radio>
                    <Radio label="FAKE">误报</Radio>
                    <Radio label="OTHERS">其他</Radio>
                  </Radio-group>
                  <div class="break-line"></div>
                  <Button type="success" size="small" long @click.prevent="processEvent('CLOSE_EVENT')">处理完成</Button>
                </div>
                <div  style="text-align: center" v-if="sensorEvent.processStatus == 'CLOSE'">
                  <Radio-group v-model="sensorEvent.processConfirmation">
                    <Radio label="REAL" disabled>真实</Radio>
                    <Radio label="FAKE" disabled>误报</Radio>
                    <Radio label="OTHERS" disabled>其他</Radio>
                  </Radio-group>
                  <div class="break-line"></div>
                  <Button type="error" size="small" long @click.prevent="processEvent('REOPEN_EVENT')">重新处理</Button>
                </div>
                <div class="break-line"></div>
                <div style="font-size: 12px">处理记录：</div>
                <div class="break-line"></div>
              </div>
              <div ref="evtLogContainer" v-bind:style="{height: processLogContainerHeight}">
                <!-- 事件处理记录-->
                <ul ref="evtLogBody" style="height: 100%;overflow: auto;">
                  <div v-if="sensorEvent.processLog != undefined">
                    <li v-for="log in sensorEvent.processLog" style="border-bottom: 1px dashed gray; padding-top:5px; padding-bottom: 5px;">
                      <div>
                        <span v-if="log.processAction == 'OPEN_PROCESS'" style=" word-break: normal; width: auto; display: block; white-space: pre-wrap; word-wrap: break-word; overflow: hidden;"><Icon type="android-alert" color="#ff3535" size="16" style="vertical-align: middle; margin-right: 6px;"></Icon>{{log.processComment}}</span>
                        <span v-if="log.processAction == 'START_PROCESS'" style=" word-break: normal; width: auto; display: block; white-space: pre-wrap; word-wrap: break-word; overflow: hidden;"><Icon type="ios-play-outline" color="#0f86d1" size="18" style="vertical-align: middle; margin-right: 6px;"></Icon>{{log.processComment}}</span>
                        <span v-if="log.processAction == 'CLOSE_EVENT'" style=" word-break: normal; width: auto; display: block; white-space: pre-wrap; word-wrap: break-word; overflow: hidden;"><Icon type="android-checkmark-circle" color="#02c3a5" size="16" style="vertical-align: middle; margin-right: 6px;"></Icon>{{log.processComment}}</span>
                        <span v-if="log.processAction == 'REOPEN_EVENT'" style=" word-break: normal; width: auto; display: block; white-space: pre-wrap; word-wrap: break-word; overflow: hidden;"><Icon type="ios-play-outline" color="#0f86d1" size="18"style="vertical-align: middle; margin-right: 6px;"></Icon>{{log.processComment}}</span>
                        <span v-if="log.processAction == 'COMMENT'" style=" word-break: normal; width: auto; display: block; white-space: pre-wrap; word-wrap: break-word; overflow: hidden;"><Icon type="ios-cloud-upload-outline" color="#02c3a5 " size="16"style="vertical-align: middle; margin-right: 6px;"></Icon>{{log.processComment}}</span>
                        <span style="float: left;  width: 45%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;" :title="log.processorName"><Icon type="android-person" style="margin-right: 6px;" size="15"></Icon>{{log.processorName}}</span>
                        <span style="float: right; width: 45%;overflow: hidden; text-overflow: ellipsis; white-space: nowrap;" :title="common.revertMTime(log.processDate)">{{common.revertMTime(log.processDate)}}</span>
                      </div>
                      <div style="clear: both">
                      </div>
                    </li>
                  </div>
                </ul>
              </div>
              <div class="break-line"></div>
              <div>
                <!-- comments -->
                <Input @on-click="processEvent('COMMENT')" @on-enter="processEvent('COMMENT')" v-model="logMessage" icon="ios-clock-outline" placeholder="请输入处理记录..." style="width: 100%"></Input>
              </div>
            </Card>
          </div>
        </Col>
        <Col style="margin-top: 10px; width: 45%; float: left;">
          <!-- 事件信息 -->
          <div ref="modalLeftBottomCol">
            <Card dis-hover class="small" style="min-height: 220px;" >
              <p slot="title">事件信息</p>
              <div>
                <Row :gutter="8">
                  <Col span="6" title="事件类型" style="text-align: right;">事件类型:</Col>
                  <Col span="18">{{selectedEventType}}</Col>
                </Row>
                <Row :gutter="8">
                  <Col span="6" title="处理状态" style="text-align: right">处理状态:</Col>
                  <Col span="18">{{selectedProcessStatus}}</Col>
                </Row>
                <Row :gutter="8">
                  <Col span="6" title="事件发生时间" style="text-align: right">发生时间:</Col>
                  <Col span="18">{{common.revertMTime(sensorEvent.issueTime)}}</Col>
                </Row>
                <Row :gutter="8">
                  <Col span="6" title="事件发生原因" style="text-align: right">发生原因:</Col>
                  <Col span="18">{{selectedConfirmationType}}</Col>
                </Row>
              </div>
            </Card>
          </div>
        </Col>
      </Row>
    </Modal>

    <!-- 联系人弹出 -->
    <UserModal :sensorId="sensor.id" :userModal="userModal" v-on:closeUserModal="closeUserModal" v-if="sensor != null"></UserModal>

  </div>
</template>

<script>
  import common from '../libs/common'
  import Vue from 'vue';
  import UserModal from '../components/sensor-contacts.vue';
  import config from '../config/config';
  import server from "../config/api";

  export default {
    computed: {
      currentStep: function(){
        switch(this.sensorEvent.processStatus) {
          case 'CLOSE': return 2;
          case 'PENDING': return 1;
          case 'OPEN': return 0;

        }
      },
      selectedEventType: function() {
        switch(this.sensorEvent.eventType) {
          case 'NORMAL': return '正常';
          case 'ALARM': return '告警';
          case 'OFFLINE': return '离线';
          case 'FAULT': return '故障';
          case 'WARN': return '警告';

        }
      },
      processLogContainerHeight: function(){
         if(this.sensorEvent.processStatus == 'OPEN'){
             return '270px';
         }else{
             return '238px';
         }
      },
      selectedProcessStatus: function() {
        switch(this.sensorEvent.processStatus) {
          case 'CLOSE': return '处理完成';
          case 'PENDING': return '处理中';
          case 'OPEN': return '事件发生(未处理)';

        }
      },
      selectedConfirmationType: function() {
        switch(this.sensorEvent.processConfirmation) {
          case 'REAL': return '真实';
          case 'FAKE': return '误报';
          case 'OTHERS': return '其他';
          default: return '未知';

        }
      },
      selectedSensorStatus: function(){
        switch(this.sensor.status){
          case 'NORMAL': return '正常';
          case 'ALARM' : return '火警';
          case 'OFFLINE': return '离线';
          case 'FAULT': return '故障';
        }
      }
    },
    name: 'SensorModal',
    // 子组件参数
    // sensorDetail; sensorEvent对象
    // sensorEventModal: 是否显示modal，与父组件中的data双向绑定
    props: ['sensorEvent', 'monitorCenterId', 'sensorEventModal'],
    data() {
        return {
          isShow: false,
          common: common,
          sensor:{
            mainContact:{}
          },
          /* 设备联系人弹出层 */
          userModal: false,
          /* END 设备联系人弹出层 */
          logMessage: undefined
        }
    },
    methods: {
      loadSensor:function(sensorId, monitorCenterId){
          this.$http.get(server.api.monitor_center.sensor.get.replace('{monitorCenterId}',monitorCenterId).replace('{sensorId}',sensorId)).then((response)=>{
             this.sensor = response.body.data;
          },(error)=>{
             this.$Message.error('读取设备信息失败');
          });
      },
      showUserModal: function () {
        // 显示联系人弹出层
        this.userModal = true;
      },
      closeUserModal: function () {
        // 关闭联系人弹出层
        this.userModal = false;
      },
      closeModal: function () {
        // 关闭处理事件弹出层
        this.$emit('closeProcessModal');
      },
      processEvent: function(processAction) {
        // 处理事件 sensorEvent.processConfirmation
        if(processAction == 'CLOSE_EVENT' && this.sensorEvent.processConfirmation == undefined) {
          this.$Message.error('请选择事件原因！');
          return;
        } else if(processAction == 'COMMENT') {
          this.$http.post(server.api.monitor_center.event.addComment.replace('{eventId}', this.sensorEvent.id),
            this.logMessage
          ).then((response)=>{
            this.$Message.success('添加处理记录成功！');
            // push刚添加的log
            this.sensorEvent.processLog.push({
              'processAction': 'COMMENT',
              'processorName': this.$store.state.user.name,
              'processComment': this.logMessage,
              'processDate': Date.parse(new Date())
            });
            // 处理事件
            this.$emit('eventUpdate', this.sensorEvent);
            this.logMessage = '';
          },(error)=>{
            this.$Message.error('事件处理失败！');
          });
          return;
        }else{
          this.$http.post(server.api.monitor_center.event.process.replace('{eventId}', this.sensorEvent.id),{
            'action': processAction,
            'comment': this.logMessage,
            'confirmaiton': this.sensorEvent.processConfirmation
          }).then((response)=>{
            this.$Message.success('处理事件成功！');
            this.sensorEvent.processStatus = response.body.data.processStatus;
            this.sensorEvent.processConfirmation = response.body.data.processConfirmation;
            this.sensorEvent.processLog.push(response.body.data.processLog[response.body.data.processLog.length-1]);
            this.sensorEvent.processTime = response.body.data.processTime;
            this.sensorEvent.closeTime = response.body.data.closeTime;
            this.$emit('eventUpdate', this.sensorEvent);
          },(error)=>{
            this.$Message.error('处理事件失败！');
          });
        }
      },
      resizeDynamicListHeight: function () {
        // 调整右侧栏高度
//        this.$refs.modalRightCol.childNodes.item(0).style.minHeight = ((this.$refs.modalLeftBottomCol.getBoundingClientRect().height) + 10 + (this.$refs.modalLeftTopCol.getBoundingClientRect().height)) + 'px';
//        this.$refs.evtLogBody.style.height = (this.$refs.evtLogContainer.getBoundingClientRect().height) + 'px';

      }
    },
    watch: {
      // 监听是否打开modal，如果打开，进行自动调整弹出层右侧高度
      isShow: function(val, oldVal){
        if (val == true) {
          this.loadSensor(this.sensorEvent.sensor.id, this.monitorCenterId);
          setTimeout(() => {
            this.resizeDynamicListHeight();
          }, 250);
        } else {
          // 如果是关闭了，需要给父组件传递消息，否则父组件的modal参数一直为true
          this.closeModal();
        }

      },
      sensorEventModal: function (val, oldVal) {
          this.isShow = val;
      }
    },
    components:{
      'UserModal': UserModal
    }
  }
</script>



