<script>
  import server from "../../config/api";
  import common from '../../libs/common';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';
  import DeviceModal from '../../components/device-modal.vue';
  import SockJS from 'sockjs-client';
  import Stomp from 'webstomp-client';

  let socket;
  let stompClient;

  export default {
    components: {
      'DeviceModal': DeviceModal,
    },
    data() {
      return {
        mapType: 'list',
        common: undefined,
        deviceDetailModal: false,
        deviceEvent: [],
        selectedEvent: {},
        organizationId: undefined,
        includeSubOrgs: false,
//        concerned: false,
//        includeAbnormalBlds: false,
        eventDetailImg: require("../../assets/event/event_detail.png"),
        eventDetailPassiveImg: require("../../assets/event/event_detail.png"),
        eventDetailActiveImg: require("../../assets/event/event_detail_active.png"),
        alarm_sound: require('../../assets/alarm.wav'),
        buildingSystemStatusList: [],
        currentOrgName: localStorage.current_org_name,

        eventTypeNormal: require("../../assets/event/event_type_normal.png"),
        eventTypeFire: require("../../assets/event/event_type_fire.png"),
        eventTypeFault: require("../../assets/event/event_type_fault.png"),
        eventTypeShield: require("../../assets/event/event_type_shield.png"),
        eventTypeWarn: require("../../assets/event/event_type_warn.png"),
        eventTypeStart: require("../../assets/event/event_type_start.png"),
        eventTypeFeedBack: require("../../assets/event/event_type_feedback.png"),
      };
    },
    methods: {
      showBuildingMap: function () {
        this.$router.push('/monitor');
      },
      showBuildingDetail: function (buildingId) {
        this.clearYunSession('route_list');
        let page = {};
        page.router = {
          path: '/building_card',
        };
        this.pushYunSession('route_list', page);
        // 记录将跳转的页面的数据信息
        page = {
          router: {
            path: '/building_detail',
          },
          mcBuildingId: buildingId
        };
        this.pushYunSession('route_list', page);
        this.$router.push(page.router);
      },
      // 查看设备事件详情
      showDeviceEventDetail: function (event) {
        this.selectedEvent = event;
        // @Liucz 设备查看详情时，自动标记为被选中状态
//        this.onAlarmSensorClick(sensor, true);
        Vue.nextTick(() => {
          this.deviceDetailModal = true;
        });
      },
      closeDeviceDetailModal: function () {
        this.deviceDetailModal = false;
      },
      showEventHistory: function () {
        this.clearYunSession('route_list');
        let page = {};
        page.router = {
          path: '/building_detail',
        };
        this.pushYunSession('route_list', page);
        // 记录将跳转的页面的数据信息
        page = {
          router: {
            path: '/event',
          }
        };
        this.pushYunSession('route_list', page);
        this.$router.push(page.router);
      },
      handleData: function () {
        for (let index in this.buildingSystemStatusList) {
          let building = this.buildingSystemStatusList[index];
          this.handelSystemStatus(building, 'ms_fas', 'fas');
          this.handelSystemStatus(building, 'ms_fire_water_system', 'water');
          this.handelSystemStatus(building, 'ms_electric_fire_monitor_system', 'electric');
          this.handelSystemStatus(building, 'ms_auto_fire_ext_system', 'fire');
          this.handelSystemStatus(building, 'ms_indoor_fire_hydrant_system', 'hydrant');
          this.handelSystemStatus(building, 'ms_smoke_control_category', 'smoke');
          this.handelSystemStatus(building, 'ms_fire_power', 'power');
          this.handelSystemStatus(building, 'ms_fire_evacuation_system', 'evacuation');
          building.others = {
            abnormalCnt: 0,
            totalCnt: 0
          }
          for (let jndex in building.systemStatusCnt) {
            let other = building.systemStatusCnt[jndex];
            building.others.abnormalCnt += other.abnStatusCnt;
            building.others.totalCnt += other.totalStatusCnt;
          }
        }
      },
      getBuildingStatus: function (building) {
        if (building.fireCnt > 0) {
          return 'FIRE';
        }
        if (building.faultCnt > 0 || building.warnCnt > 0 || building.shieldCnt > 0) {
          // 异常
          return 'ABNORMAL';
        }
        return 'NORMAL';
      },
      handelSystemStatus: function (building, systemCode, statusName) {
        let index = _.findIndex(building.systemStatusCnt, {systemCode: systemCode});
        if (index === -1) {
          building[statusName] = {abnormalCnt: 0, totalCnt: 0};
        } else {
          building[statusName] = {
            abnormalCnt: building.systemStatusCnt[index].abnStatusCnt,
            totalCnt: building.systemStatusCnt[index].totalStatusCnt,
          };
          // 从systemStatusCnt中删除，用来查询其他
          building.systemStatusCnt.splice(index, 1);
        }
      },
      initData: function () {
        // 获取左侧列表建筑物报警状态
        this.searchBuildingStatus();
        // 获取事件
        this.searchEventList();
      },
      searchBuildingStatus: function () {
        this.$http.get(server.api.organization.buildingList.systemStatus.replace('{orgId}', this.organizationId) + '?includeSubOrgs=' + this.includeSubOrgs).then((response) => {
          if (!response.body.meta.success) {
            this.$Message.error("获取建筑物状态失败！");
            return;
          }
          this.buildingSystemStatusList = response.body.data;
        }, (error) => {
          this.$Message.error("获取建筑物状态失败！");
        });
      },
      searchEventList: function () {
        // 先查询设备事件、再查询装置事件，最后进行排序
        this.$http.get(server.api.organization.event.get + '?level=ORG&orgId=' + this.organizationId + '&pageSize=50&pageNum=1&includeSubOrgs=' + this.includeSubOrgs).then((response) => {
          let deviceEventList = response.body.data.rows;
          deviceEventList = this.deviceEvent.concat(deviceEventList);
          this.deviceEvent = _.orderBy(deviceEventList, 'creationDt', 'desc');
        }, (error) => {
          this.$Message.error("获取事件列表失败！");
        });

        this.$http.get(server.api.organization.event.gateway + '?level=ORG&orgId=' + this.organizationId + '&pageSize=50&pageNum=1&includeSubOrgs=' + this.includeSubOrgs).then((responseB) => {
          let gatewayEventList = responseB.body.data.rows;
          gatewayEventList = this.deviceEvent.concat(gatewayEventList);
          this.deviceEvent = _.orderBy(gatewayEventList, 'creationDt', 'desc');
        }, (error) => {
          this.$Message.error("获取事件列表失败！");
        });
      },
      changeIncludeSubOrgs: function (status) {
        // 是否查询下级单位
        localStorage.includeSubOrgs = Boolean(status);
        this.searchBuildingStatus();
      },
      updateBuildingStatus: function (deviceEvent) {
        if (deviceEvent.pushEventType === 'NEW') {
          // 新事件发生，重新请求获取建筑物信息
          this.$http.get(server.api.organization.building.status.replace('{buildingId}', deviceEvent.buildingId)).then((response) => {
            let newBuildingStatus = response.body.data;
            let index = _.findIndex(this.buildingSystemStatusList, {'buildingId': deviceEvent.buildingId});
            if (index === -1) {
              // 列表中不存在这个建筑物
              this.buildingSystemStatusList.splice(0, 0, newBuildingStatus);
            } else {
              Vue.set(this.buildingSystemStatusList, index, newBuildingStatus);
            }
          }, (error) => {
            this.$Message.error("更新建筑物信息失败！");
          });
        }
      },
      selectEventStatus: function (eventType) {
        // 关于网关，所有 OFF 类型均使用正常图标
        switch (eventType) {
          case 'NORMAL':
          case 'GW_MANUAL_ALARM_OFF':
          case 'GW_FIRE_OFF':
          case 'GW_FAULT_OFF':
          case 'GW_P_POWER_FAULT_OFF':
          case 'GW_S_POWER_FAULT_OFF':
          case 'GW_OFFLINE_OFF':
          case 'GW_WIRE_BREAK_OFF':
            return this.eventTypeNormal;
          case 'FIRE':
          case 'GW_OP_MANUAL_ALARM':
          case 'GW_FIRE_ON':
            return this.eventTypeFire;
          case 'FAULT':
          case 'GW_FAULT_ON':
          case 'GW_P_POWER_FAULT_ON':
          case 'GW_S_POWER_FAULT_ON':
          case 'GW_OFFLINE_ON':
          case 'GW_WIRE_BREAK_ON':
            return this.eventTypeFault;
          case 'SHIELD':
            return this.eventTypeShield;
          case 'WARN':
            return this.eventTypeWarn;
          case 'START':
            return this.eventTypeStart;
          case 'FEEDBACK':
            return this.eventTypeFeedBack;
        }
      },
      isFasOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.fasExpCnt >= 10 && building.fasTotalCnt >= 1000) {
          return true;
        }
        if (building.fasExpCnt >= 100 && building.fasTotalCnt >= 100) {
          return true;
        }
        if (building.fasExpCnt >= 0 && building.fasTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isWaterOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.waterExpCnt >= 10 && building.waterTotalCnt >= 1000) {
          return true;
        }
        if (building.waterExpCnt >= 100 && building.waterTotalCnt >= 100) {
          return true;
        }
        if (building.waterExpCnt >= 0 && building.waterTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isElecOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.elecExpCnt >= 10 && building.elecTotalCnt >= 1000) {
          return true;
        }
        if (building.elecExpCnt >= 100 && building.elecTotalCnt >= 100) {
          return true;
        }
        if (building.elecExpCnt >= 0 && building.elecTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isFireOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.fireExtExpCnt >= 10 && building.fireExtTotalCnt >= 1000) {
          return true;
        }
        if (building.fireExtExpCnt >= 100 && building.fireExtTotalCnt >= 100) {
          return true;
        }
        if (building.fireExtExpCnt >= 0 && building.fireExtTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isHydrantOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.hydrantExpCnt >= 10 && building.hydrantTotalCnt >= 1000) {
          return true;
        }
        if (building.hydrantExpCnt >= 100 && building.hydrantTotalCnt >= 100) {
          return true;
        }
        if (building.hydrantExpCnt >= 0 && building.hydrantTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isExhaustOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.exhaustExpCnt >= 10 && building.exhaustTotalCnt >= 1000) {
          return true;
        }
        if (building.exhaustExpCnt >= 100 && building.exhaustTotalCnt >= 100) {
          return true;
        }
        if (building.exhaustExpCnt >= 0 && building.exhaustTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isPowerOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.powerExpCnt >= 10 && building.powerTotalCnt >= 1000) {
          return true;
        }
        if (building.powerExpCnt >= 100 && building.powerTotalCnt >= 100) {
          return true;
        }
        if (building.powerExpCnt >= 0 && building.powerTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isEvacuationOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.evacuationExpCnt >= 10 && building.evacuationTotalCnt >= 1000) {
          return true;
        }
        if (building.evacuationExpCnt >= 100 && building.evacuationTotalCnt >= 100) {
          return true;
        }
        if (building.evacuationExpCnt >= 0 && building.evacuationTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      isOtherOverFlow: function (building) {
        // 数字放不下的时候，显示 /..
        if (building.otherExpCnt >= 10 && building.otherTotalCnt >= 1000) {
          return true;
        }
        if (building.otherExpCnt >= 100 && building.otherTotalCnt >= 100) {
          return true;
        }
        if (building.otherExpCnt >= 0 && building.otherTotalCnt >= 10000) {
          return true;
        }
        return false;
      },
      updateEventList: function (deviceEvent) {
        // 右侧事件列表更新，暂时不考虑是否是新事件，列表中只要有就会被pop，然后加一条最新的放在最上面
        let eventIndex = _.findIndex(this.deviceEvent, {eventId: deviceEvent.eventId});
        if (eventIndex !== -1) {
          this.deviceEvent.splice(eventIndex, 1);
        }
        this.deviceEvent.splice(0, 0, deviceEvent);
//        // 列表长度过长时，去掉末尾
//        if (this.rtEvents.length > 1000) {
//          this.rtEvents.splice(999, this.rtEvents.length - 1000);
//        }
      },
      playAlarmSound: function () {
        //  this.$refs.player.play();
        let audio = document.querySelector('#player');
        audio.play();
      },
      /***WebScoket初始化方法***/
      //---------Web socket begin---------
      connectWSServer: function () {
        socket = new SockJS(server.ws.subscribeEndPoint);
        stompClient = Stomp.over(socket);
        stompClient.connect({}, this.onWSConnected, this.onWSFail);
      },
      disconnectWSServer: function () {
        stompClient.disconnect(() => function () {
          console.info('Websocket disconnected');
        });
      },
      onWSConnected: function (frame) {
        console.log('WS Connected: ' + frame);
        // 进行机构级别的事件监听
        stompClient.subscribe(server.ws.orgInfoChangeTopic.replace('{orgId}', this.organizationId), (data) => {
          console.log('Received Data From Server: ' + data);
          let deviceEvent = JSON.parse(data.body);
          // 推送事件。先判断是否是当前监控中心信息，再判断是否开启 显示下级监控中心开关
          if (deviceEvent.orgId !== this.organizationId && !this.includeSubOrgs) {
            return;
          }
          if(deviceEvent.eventSource === 'DEVICE') {
            // 设备类型事件处理
            this.handleDeviceEvent(deviceEvent);
          } else if(deviceEvent.eventSource === 'GATEWAY') {
            // 传输装置类型事件处理
            this.handleTranceiverEvent(deviceEvent);
          }
          // 需要进行音频通知、事件列表闪烁的事件
          if((deviceEvent.pushEventType === 'NEW' || deviceEvent.processStatus === 'OPEN')
            && (deviceEvent.eventType === 'FIRE' || deviceEvent.eventType === 'GW_OP_MANUAL_ALARM' || deviceEvent.eventType === 'GW_FIRE_ON')) {
            this.playAlarmSound();
            deviceEvent.newUpdate = true;
            document.getElementById('event_list').scrollTop = 0;
          }
          // 更新左侧列表
          this.updateBuildingStatus(deviceEvent);
          // 更新右侧列表
          this.updateEventList(deviceEvent);
        })
      },
      onWSFail: function (frame) {
        //Callback for connected failed
        console.log('WS Failed:' + frame);
        setTimeout(this.connectWSServer, 10000);
        console.log('STOMP: Reconnecting in 10 seconds');
      },
      /***END WebScoket初始化方法***/

      handleDeviceEvent: function(deviceEvent) {
        if (deviceEvent.processStatus === 'OPEN') {
          // 发生新事件，通知提醒
          let title = undefined;
          if (deviceEvent.eventType === 'FIRE') {
            this.$Notice.error({
              title: '设备报警！',
              desc: deviceEvent.buildingName + '-' + deviceEvent.deviceName,
              duration: 0
            });
          } else {
            switch (deviceEvent.eventType) {
              case 'FAULT':
                title = '设备出现故障';
                break;
              case 'WARN':
                title = '设备发生告警';
                break;
              case 'OFFLINE':
                title = '设备离线';
                break;
            }
            if(title !== undefined) {
              this.$Notice.warning({
                title: title,
                desc: deviceEvent.buildingName + '-' + deviceEvent.deviceName,
              });
            }
          }
        }
      },
      handleTranceiverEvent: function(tranceiverEvent) {
        // 判断device_modal是否需要进行相关刷新
        if (this.deviceDetailModal && this.selectedEvent.deviceId === tranceiverEvent.deviceId) {
          this.tranceiverWS(tranceiverEvent);
        }
        if (tranceiverEvent.processStatus === 'OPEN') {
          if(tranceiverEvent.eventType === 'GW_OP_MANUAL_ALARM' || tranceiverEvent.eventType === 'GW_FIRE_ON') {
            // 右上角的notice，可点击类型
            this.$Notice.error({
              title: '传输装置火警！',
              duration: 0,
              render: h => {
                return h('span', [
                  tranceiverEvent.buildingName + '-' + tranceiverEvent.deviceName,
                  h('i-button', {
                    props: {
                      type: 'text',
                      size: 'small'
                    },
                    style: {
                      color: '#3399ff'
                    },
                    on: {
                      click: () => {
                        this.showDeviceEventDetail(tranceiverEvent);
                      }
                    }
                  }, '查看详情')
                ]);
              }
            });
          } else if(tranceiverEvent.eventType === 'GW_FAULT_ON' || tranceiverEvent.eventType === 'GW_P_POWER_FAULT_ON'
            || tranceiverEvent.eventType === 'GW_S_POWER_FAULT_ON' || tranceiverEvent.eventType === 'GW_OFFLINE_ON'
            || tranceiverEvent.eventType === 'GW_WIRE_BREAK_ON') {
            // 右上角的notice，可点击类型
            this.$Notice.warning({
              title: '传输装置故障！',
              render: h => {
                return h('span', [
                  tranceiverEvent.buildingName + '-' + tranceiverEvent.deviceName,
                  h('i-button', {
                    props: {
                      type: 'text',
                      size: 'small'
                    },
                    style: {
                      color: '#3399ff'
                    },
                    on: {
                      click: () => {
                        this.showDeviceEventDetail(tranceiverEvent);
                      }
                    }
                  }, '查看详情')
                ]);
              }
            });
          }
        }
      },
      tranceiverWS: function(event) {
        // 有关传输装置的ws推送，向device_modal发送指令
        // 命令其做对应的数据刷新
        this.$refs.device_modal.getWebsocketPush(event);
      },
    },
    beforeRouteLeave(to, from, next) {
      this.disconnectWSServer();
      next();
    },
    mounted() {
      this.organizationId = localStorage.current_org_id;
      this.includeSubOrgs = localStorage.includeSubOrgs === 'true';
      this.common = common;
      // websocket 初始化
      this.connectWSServer();
      this.initData();
      let that = this;
      Vue.nextTick(() => {
        $("#event_list").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "4px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        $("#building_card_list").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "4px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        $(".event-detail").hover(function () {
          that.eventDetailImg = that.eventDetailActiveImg;
        }, function () {
          that.eventDetailImg = that.eventDetailPassiveImg;
        });
      })
    }

  }
</script>



