function tryMove() {
  // 点开用户权限编辑页面
  let isMouseDown = false; //鼠标是否按下
  let mouseDownPosiX;
  let mouseDownPosiY;
  let InitPositionX;
  let InitPositionY;
  let obj;
  $(".ivu-modal-header").mousedown(function (e) {
    obj = $(this).parents(".ivu-modal");
    //当鼠标按下时捕获鼠标位置以及对象的当前位置
    mouseDownPosiX = e.pageX;
    mouseDownPosiY = e.pageY;
    isMouseDown = true;

    InitPositionX = obj.css("left").replace("px", "");
    InitPositionY = obj.css("top").replace("px", "");
    if(InitPositionX === '0' || InitPositionX === 'auto') {
      // 自适应位置
      InitPositionX = (document.body.clientWidth - parseInt(obj.css("width").replace("px", ""))) / 2;
      obj.css("margin", "0");
      obj.css("left", InitPositionX + "px").css("top", InitPositionY + "px");
    }

  }).mousemove(function (e) {
    //当鼠标按下并且移动时，首先判断鼠标是否在当前焦点，以及鼠标是否已经弹起，
    if (isMouseDown) {
      let tempX = parseInt(e.pageX) - parseInt(mouseDownPosiX) + parseInt(InitPositionX);
      let tempY = parseInt(e.pageY) - parseInt(mouseDownPosiY) + parseInt(InitPositionY);
      obj.css("left", tempX + "px").css("top", tempY + "px");
    }
    //当鼠标弹起或者离开元素时，将鼠标弹起置为false，不移动对象
  }).mouseup(function () {
    isMouseDown = false;
  }).mouseleave(function () {
    isMouseDown = false;
  });
}

export {
  tryMove
}



